body {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Roboto, sans-serif;
}

.header {
  justify-content: space-between;
  align-items: center;
  height: 60px;
  padding: 0 50px;
  display: flex;
}

.header__nav {
  padding: 0;
}

.header__nav-list {
  justify-content: flex-start;
  align-items: center;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.header__nav-item {
  text-transform: uppercase;
  margin-right: 20px;
  font-size: 12px;
  font-weight: 500;
}

.header__nav-item:last-child {
  margin-right: 0;
}

.header__nav-item__link {
  color: #060b35;
  line-height: 60px;
  text-decoration: none;
  display: block;
}

.header__nav-item__link:hover, .header__nav-item__link:active {
  color: #00acdc;
}

.header__nav-item__link:active {
  text-decoration: underline;
}

.header__nav-item__link.is-active {
  color: #00acdc;
  position: relative;
}

.header__nav-item__link.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 2px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
  left: 0;
}

.header__logo-link, .header__logo-link img {
  width: 40px;
  height: auto;
  display: block;
}

.catalog {
  grid-template-columns: repeat(4, 200px);
  justify-content: center;
  gap: 46px 48px;
  width: 944px;
  margin: 50px auto;
  display: grid;
}

@media (width >= 1024px) {
  .catalog {
    width: 1024px;
  }
}

@media (width >= 1200px) {
  .catalog {
    width: 1200px;
  }
}

p {
  margin: 0;
}

.card {
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 200px;
  height: 408px;
  display: flex;
}

.card:hover {
  transition: transform .3s;
  transform: scale(1.2);
}

.card__content {
  border-radius: 5px;
  width: 200px;
  height: 408px;
}

.card__image {
  background-image: url("imac.202c52ff.jpeg");
  background-size: cover;
  justify-content: center;
  align-items: center;
  width: 160px;
  height: 134px;
  margin-top: 32px;
  margin-left: 19px;
  display: flex;
}

.card__title {
  width: 166px;
  height: 36px;
  margin-top: 40px;
  margin-left: 16px;
}

.card__title-text {
  color: #060b35;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__title-text:hover {
  color: #34568b;
  transition: color .3s;
}

.card__code {
  width: 98px;
  height: 14px;
  margin-top: 4px;
  margin-left: 16px;
  line-height: 14px;
}

.card__code-text {
  color: #616070;
  font-size: 10px;
  font-weight: 400;
}

.card__rating {
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  width: 166px;
  margin-top: 16px;
  margin-left: 16px;
  display: flex;
}

.card__rating-stars {
  display: flex;
}

.card__rating-star {
  background-image: url("star.822d561e.svg");
}

.card__rating-star:first-child, .card__rating-star:nth-child(2), .card__rating-star:nth-child(3), .card__rating-star:nth-child(4) {
  background-image: url("star-active.edda4c43.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-left: 4px;
}

.card__rating-star:nth-child(5) {
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-left: 4px;
}

.card__rating-star:first-child {
  margin-left: 0;
}

.card__rating-text {
  text-align: right;
  color: #060b35;
  font-size: 10px;
  line-height: 14px;
}

.card__price {
  justify-content: center;
  align-items: center;
  width: 166px;
  height: 16px;
  margin-top: 24px;
  margin-left: 16px;
  display: flex;
}

.card__price-text {
  color: #616070;
  font-size: 12px;
  font-weight: 400;
}

.card__price-value {
  color: #060b35;
  margin-left: auto;
  font-size: 16px;
  font-weight: 700;
}

.card__buy-wrapper {
  background-color: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  width: 166px;
  height: 40px;
  margin-top: 16px;
  margin-left: 16px;
  display: flex;
}

.card__buy-wrapper a.card__buy {
  color: #fff;
  text-transform: uppercase;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  font-size: 14px;
  font-weight: 700;
  text-decoration: none;
  display: flex;
}

.card__buy-wrapper:hover {
  background-color: #fff;
}

.card__buy-wrapper:hover a.card__buy {
  color: #00acdc;
}
